.modal.show {
  display: block !important;
}

.modal-backdrop.show {
  opacity: 0.5;
}

.signature-container {
  text-align: center;
}

.signature-instructions {
  background-color: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 0.375rem;
  padding: 1rem;
}

.signature-canvas-container {
  border: 2px dashed #dee2e6;
  border-radius: 0.375rem;
  padding: 1rem;
  background-color: #ffffff;
  position: relative;
  overflow: hidden;
}

.signature-canvas {
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  cursor: crosshair;
  background-color: #ffffff;
  display: block;
  margin: 0 auto;
  max-width: 100%;
  height: auto;
}

.signature-canvas:hover {
  border-color: #86b7fe;
  box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.25);
}

.signature-actions {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
}

/* RTL Support */
.signature-canvas.rtl {
  direction: rtl;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .modal-dialog {
    margin: 0.5rem;
  }
  
  .signature-canvas {
    width: 100%;
    height: 150px;
  }
  
  .signature-actions {
    flex-direction: column;
    align-items: center;
  }
  
  .signature-actions .btn {
    width: 100%;
    margin-bottom: 0.5rem;
  }
}

/* Touch device optimizations */
@media (hover: none) and (pointer: coarse) {
  .signature-canvas {
    touch-action: none;
  }
}

/* Signature pad specific styles */
.signature-canvas-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    linear-gradient(to right, #f0f0f0 1px, transparent 1px),
    linear-gradient(to bottom, #f0f0f0 1px, transparent 1px);
  background-size: 20px 20px;
  opacity: 0.3;
  pointer-events: none;
  z-index: 1;
}

.signature-canvas {
  position: relative;
  z-index: 2;
}

/* Animation for signature pad */
.signature-canvas-container {
  transition: all 0.3s ease;
}

.signature-canvas-container:hover {
  border-color: #86b7fe;
  box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.1);
}

/* Button styles */
.btn-outline-secondary {
  border-color: #6c757d;
  color: #6c757d;
}

.btn-outline-secondary:hover {
  background-color: #6c757d;
  border-color: #6c757d;
  color: #fff;
}

.btn-success {
  background-color: #198754;
  border-color: #198754;
}

.btn-success:hover {
  background-color: #157347;
  border-color: #146c43;
}

/* Modal header styling */
.modal-header.bg-primary {
  background-color: #D4AF5F !important;
}

.modal-title {
  font-weight: 600;
}

/* Instructions styling */
.signature-instructions p {
  margin: 0;
  font-size: 0.9rem;
}

.signature-instructions i {
  color: #0d6efd;
}

/* Approval notes section styling */
.approval-notes-section {
  background-color: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 0.375rem;
  padding: 1rem;
  margin-top: 1rem;
}

.approval-notes-section h6 {
  color: #495057;
  font-weight: 600;
  margin-bottom: 0.75rem;
}

.approval-notes-section textarea {
  resize: vertical;
  min-height: 80px;
}

.approval-notes-section .form-text {
  font-size: 0.875rem;
  margin-top: 0.25rem;
}
